import React, { useState } from "react";
import { Add } from "@mui/icons-material";
import { Typography, Box, Divider, Button } from "@mui/material";

import Helmet from "react-helmet";
import { useQuery } from "react-query";
import Content from "../components/Content";
import AddLecturer from "../components/modals/AddLecturer";
import CustomTable from "../components/tables/CustomTable";
import { lecturerColumns } from "../components/data/columns";

import { getAllLecturers } from "../components/api/lecturerApi";
function Lecturer() {
  const [openAddLecturer, setOpenAddLecturer] = useState(false);
  const [lecturerErr, setLecturerErr] = useState(false);

  const lecturers = useQuery(["lecturer"], getAllLecturers, {
    retry: 3,
    onSuccess: (data) => {},
    onError: (error) => {
      // alert("Error occured while fetching data");
      if (error === undefined) {
        setLecturerErr(
          `Connection failed.We can’t connect to the server.Try again later`
        );
        return;
      }
      // console.log(error);
      setLecturerErr(error);
    },
  });

  return (
    <section>
      <Helmet>
        <title>Results System | Lecturer</title>
      </Helmet>
      <Content>
        <section>
          <Typography variant="h4">Lectures</Typography>
          <Divider />
          <Box
            sx={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          ></Box>
        </section>
        <Box display="flex" justifyContent="flex-end" paddingY={4}>
          <Button
            variant="contained"
            color="secondary"
            startIcon={<Add />}
            onClick={() => setOpenAddLecturer(true)}
          >
            Add New
          </Button>
        </Box>
        <CustomTable
          heading="Lecturers"
          columns={lecturerColumns}
          data={lecturers.data ? lecturers.data : []}
          refresh={lecturers.refetch}
        />
      </Content>
      <AddLecturer open={openAddLecturer} setOpen={setOpenAddLecturer} />
    </section>
  );
}

export default Lecturer;
